import { required, NumericValueType, numeric, prop, maxLength } from '@rxweb/reactive-form-validators';

export class DuyurularPage {
  Rows: Duyurular[];
  Count: number;
}

export class Duyurular {
  @prop()
  SiraNo?: number;
  @prop()
  OrderNo?: number;
  @required()
  Baslik?: string;
  @required()
  Aciklama?: string;
  @prop()
  Tarih?: Date;
  @prop()
  KullaniciID?: number;
  @prop()
  BirimID?: number;
}
export class DuyurularJoin {
  SiraNo?: number;
  OrderNo?: number;
  Baslik?: string;
  Aciklama?: string;
  Tarih?: Date;
  KullaniciID?: number;
  BirimID?: number;
  KullaniciAdi: string;
  BirimAdi: string;
}
